body{
  background-color: #000;
  font-family: 'Roboto', sans-serif;
}

.App{
  padding: 5.5em 0 0 0;
}

.overlay {
  position: fixed; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.5); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
}

.circleWrapper{
  height: 14em;
  width: 100%;
}

.circle{
  box-shadow: 0 0 45px 15px #BD4141;
}

.circle-4{
  margin: 0 auto;
  height: 14em;
  width: 14em;
  background-color: #000;
  border: 1em solid #A83434;
  border-radius: 50%;
  visibility: hidden;
  transition: visibility 2s ease-out;
}

.circle-3{
  margin: -14em auto 0 auto;
  height: 10em;
  width: 10em;
  border: 1em solid #A83434;
  border-radius: 50%;
  visibility: hidden;
  transition: visibility 2s ease-out;
}

.circle-2{
  margin: -10em auto 0 auto;
  height: 6em;
  width: 6em;
  border: 1em solid #A83434;
  border-radius: 50%;
  visibility: hidden;
  transition: visibility 2s ease-out;
}

.circle-1{
  margin: -6em auto 0 auto;
  height: 4em;
  width: 4em;
  background-color: #A83434;
  border-radius: 50%;
  visibility: hidden;
  transition: visibility 1s ease-out;
}

.titleWrapper{
  padding: 4em 2em;
}

h1{
  font-size: 4.25em;
  /* color: #B1D8D7; */
  margin: -0.1em 0;
  font-weight: bold;
}

button{
  position: relative;
  width: 40%;
  margin: 0 auto;
  background-color: #B1D8D7;
  border-radius: 6px;
  padding: .75em 1em;
  display: block;
  visibility: visible;
  z-index: 3;
}
